@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Abril+Fatface);
html {
  /* border-box box model allows us to add padding and border to our elements without increasing their size */
  box-sizing: border-box;
  /* A system font stack so things load nice and quick! */
  font-family: 'Open Sans', Helvetica, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: white;
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.07);
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background: -webkit-linear-gradient(bottom, rgba(40,44,52,0.8) 20%, rgba(16,29,55,0.90) 40%), -webkit-linear-gradient(bottom, rgba(246,1,1,0.76) 10%, rgba(224,30,30,0.1) 40%), url(/joeljames/static/media/collab.d49404c5.jpg) center;
  background: linear-gradient(0deg, rgba(40,44,52,0.8) 20%, rgba(16,29,55,0.90) 40%), linear-gradient(0deg, rgba(246,1,1,0.76) 10%, rgba(224,30,30,0.1) 40%), url(/joeljames/static/media/collab.d49404c5.jpg) center;
  background-size: cover;
  min-height: calc(100vh - 100px);
  /* margin: 50px; */
  background-attachment: fixed;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 5px 0;
}

div {
    border: 0px;
    display: flex;
    flex-direction: column;
    /* border: 3px solid orange; */
}

#land-header {
    height: 100px;
    justify-content: flex-end;
    /* transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1); */
}

#land-header h3 {
    align-self: center;
    font-size: 2em;
    font-weight: 100;
    letter-spacing: 2px;
    color: white;
    text-transform: uppercase;
    /* transition: 2s cubic-bezier(0.075, 0.82, 0.165, 1); */
}

#land-title {
    height: 300px;
    justify-content: flex-end;
    align-items: center;
    /* animation-delay: 1s;
    transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1); */
}

#land-title h1 {
    /* border: 2px solid blue; */
    color: #d63031;
    font-family: 'Abril Fatface', cursive;
    font-weight: 500;
    font-size: 5em;
    letter-spacing: 2px;
}


p.location {
    align-self: center;
    color: white;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding-top: 100px;
    height: 200px;
    /* border: 2px solid white; */
}

#bring-up-nav {
    /* border: 2px solid orange; */
    flex-direction: row;
    align-content: center;
    justify-content: center;
    height: 100px;
}

#slide-right {
    position: absolute;
    height: 70px;
    width: 70px;
    z-index: 2;
    align-self: center;
    font-size: 40px;
    /* padding: 10px 20px; */
    color: white;
    border: 1px solid white;
    padding: 12px 24px;
}

#slide-right:hover {
    position: absolute;
    background-color: rgba(224,30,30,0.2);
    height: 70px;
    width: 70px;
    z-index: 0;
    align-self: center;
    font-size: 40px;
    /* padding: 10px 20px; */
    color: white;
    border: 1px solid white;
    padding: 12px 24px;
    -webkit-transition: 1s;
    transition: 1s;
    cursor: pointer;
}

i.fa.fa-angle-double-right {
    position: absolute;
    height: 70px;
    width: 70px;
    /* z-index: 2; */
    align-self: center;
    font-size: 40px;
    /* padding: 10px 20px; */
    color: white;
    border: 1px solid white;
    padding: 12px 24px;
}
/* Imported CSS from: Rıza Selçuk Saydam - https://codepen.io/rss/pen/vIDKH */

button.bring-up-nav {
    align-self: flex-end;
    background: transparent;
    border: none;
}

#nav-container{
    align-self: flex-end;
    /* border: 2px solid white; */
}

.toggle-icon {
	display: block;
	cursor: pointer;
}

.toggle-icon .bar {
	display: block;
	width: 3.8em;
	height: 0.2em;
    margin: 0.9em;
	border-right: 2.8em solid #d63031;
	border-left: 0.6em solid #d63031;
	box-sizing: border-box;
}
/* End imported CSS */
#flyoutMenu {
    /* border: 2px solid white; */
    position: fixed;
    right: 0;
    top: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    background: -webkit-linear-gradient(bottom, rgba(206,38,73,0.7) 50%, rgba(206,38,73,0.7) 50%), -webkit-linear-gradient(bottom, rgba(206,38,73,0.7) 50%, rgba(206,38,73,0.7) 50%), url(/joeljames/static/media/nav-background.7ee328f8.jpg) center;
    background: linear-gradient(0deg, rgba(206,38,73,0.7) 50%, rgba(206,38,73,0.7) 50%), linear-gradient(0deg, rgba(206,38,73,0.7) 50%, rgba(206,38,73,0.7) 50%), url(/joeljames/static/media/nav-background.7ee328f8.jpg) center;
    background-size: cover;
    height: 100vh;
    width: 100vw;
    color: white;
    font-family: 'Abril Fatface', cursive;
    -webkit-transition: -webkit-transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    transition: -webkit-transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    transition: transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    transition: transform 1s cubic-bezier(0.63, 0.21, 1,-0.01), -webkit-transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    z-index: 1000;
}

#flyoutMenu.hide {
    -webkit-transform: translate3d(100vw, 0, 0);
            transform: translate3d(100vw, 0, 0);
    -webkit-transition: -webkit-transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    transition: -webkit-transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    transition: transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    transition: transform 1s cubic-bezier(0.63, 0.21, 1,-0.01), -webkit-transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    background-size: cover;
    
}

#flyoutMenu.show {
    -webkit-transform: translate3d(0vw, 0, 0);
            transform: translate3d(0vw, 0, 0);
    overflow: hidden;
    background-size: cover;
}

#flyoutMenu h1 {
    margin: auto;
    font-size: 5em;
    letter-spacing: 6px;

}


